/* Hero Section */
.hero-section {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: center;
  }
  
  .video-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  
  .hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }
  
  /* Animated Text Styles */
  .animated-text-container {
    position: absolute;
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
  }
  
  .animated-text {
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: fadeInOut 2s ease forwards;
  }
  
  @keyframes fadeInOut {
    0% {
        opacity: 0;
        transform: translate(-50%, -50%) translateY(20px);
    }
    10% {
        opacity: 1;
        transform: translate(-50%, -50%) translateY(0);
    }
    30% {
        opacity: 1;
    }
    40% {
        opacity: 0;
        transform: translate(-50%, -50%) translateY(-20px);
    }
    100% {
        opacity: 0;
    }
  }
  
  .animated-text:nth-child(1) {
    animation-delay: 0s;
  }
  
  .animated-text:nth-child(2) {
    animation-delay: 2s;
  }
  
  .animated-text:nth-child(3) {
    animation-delay: 4s;
  }
  
  .animated-text:nth-child(4) {
    animation-delay: 6s;
  }
  
  .animated-text:nth-child(5) {
    animation-delay: 8s;
  }
  
  .animated-text:nth-child(6) {
    animation-delay: 10s;
  }
  
  .animated-text:nth-child(7) {
    animation-delay: 12s;
  }
  
  /* Show Hero */
  .hero-content {
    opacity: 0;
    transition: opacity 1s ease;
    position: relative;
    z-index: 3;
  }
  
  .hero-content.show-hero {
    opacity: 1;
  }
  
  .hero-content h1 {
    font-size: 5.2em;
    margin-bottom: 20px;
  }
  
  .hero-content p {
    font-size: 2em;
    margin-bottom: 40px;
  }
  
  .cta-button {
    background-color: #ff5733;
    color: white;
    padding: 15px 30px;
    font-size: 1.4em;
    margin-top: 50px;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .cta-button:hover {
    background-color: #c70039;
  }
  
  /* Social Icons */
  .social-icons {
    margin-top: 40px;
  }
  
  .social-icon {
    color: white;
    font-size: 3rem;
    margin: 0 10px;
    transition: color 0.3s ease;
  }
  
  .social-icon:hover {
    color: #f39c12;
  }
  
  /* Responsive styles */
  @media (min-width: 320px) and (max-width: 375px) {
    .hero-section {
      position: relative;
      width: 162%;
      height: 163vh;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      text-align: center;
    }
  
    .video-background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
  
    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: -1;
    }
  
    .hero-overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      z-index: 1;
    }
  
    .hero-content h1 {
      font-size: 50px; 
      margin-top: 75px;
    }
  
    .hero-content p {
      font-size: 15px; 
    }
  
    .animated-text {
      font-size: 28px !important;
    }
  
    .cta-button {
        font-size: 3vw;
        padding: 10px 20px;
    }
  
    .social-icon {
        font-size: 2rem;
    }
  }
  
  @media (min-width: 425px) and (max-width: 768px) {
    .hero-section {
      position: relative;
