/* About Section */
html {
    scroll-behavior: smooth;
  }
  
  .about-section {
    padding: 100px 20px;
    background-color: #f4f4f4;
    text-align: center;
  }
  
  .container  {
    max-width: 1700px;
    margin: 0 auto;
    padding: 0 20px;
    font-size: 1.5rem;
    margin-bottom: 40px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .importance-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 40px;
  }
  
  .importance-boxes {
    display: grid;
    width: 92%;
    grid-template-columns: repeat(auto-fill, minmax(370px, 1fr));
    gap: 20px;
  }
  
  .importance-box {
    background-color: white;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .importance-box:hover {
    transform: translateY(-10px);
  }
  
  .importance-box img.importance-icon {
    width: 170px; 
    height: 100px;
    margin-bottom: 6px;
    border-radius: 10px;
    box-shadow: 0 8px 10px rgba(0, 0, 0, 0.1);
  }
  
  .importance-box h4 {
    color: #333;
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  /* Impact Sections Toggle */
  .impact-toggle {
    display: flex;
    justify-content: center;
    margin: 40px 0;
    cursor: pointer;
  }
  
  .arrow-down,
  .arrow-up {
    font-size: 2rem;
    display: none;
    margin-bottom: 10px;
    transition: transform 0.2s ease;
  }
  
  .arrow-down {
    display: block;
  }
  
  .impact-sections {
    display: none;
  }
  
  .impact-container {
    margin-top: 20px;
  }
  
  .impact-box {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .impact-box:hover {
    transform: translateY(-10px);
  }
  
  .impact-box h3 {
    color: #333;
    font-size: 1.4rem;
    margin-bottom: 10px;
  }
  
  
  /* Responsive styles */
  @media (max-width: 360px){
    .about-section {
      padding: 20px 10px;
      width: 153%; 
      height: auto; 
      background-color: #f4f4f4;
      text-align: center;
    }
  
    .container  {
      max-width: 1700px;
      margin: 0 auto;
      padding: 0 20px;
      font-size: 1.2rem;
      margin-bottom: 40px;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
  
    .importance-box {
      background-color: white;
      padding: 20px;
      border-radius: 8px;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
      transition: transform 0.3s ease;
    }
  
    /* Impact Section */
    .importance-boxes {
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); 
        gap: 20px;
    }
  
    .impact-sections {
        display: block;
    }
  
    .footer {
      background-color: #333;
      color: #fff;
      padding: 10px 0;
      text-align: center;
      width: 163%; 
    }
      
    .footer .container {
        margin: 0 auto;
    }
      
    .footer p {
        margin: 0;
        font-size: 15px; 
    }
  
    .cta-button {
      padding: 10px 20px;
      font-size: 14px; 
    }
  }
  
