.navbar {
    background-color: #333;
    color: white;
    padding: 2rem 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    transition: background-color 0.3s ease;
  }
  
  .navbar .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1800px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .navbar .logo {
    font-size: 30px;
    text-transform: uppercase;
    text-decoration: none;
    color: #f39c12;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  }
  
  .navbar .nav-links {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .navbar .nav-links li {
    margin: 0 15px;
  }
  
  .navbar .nav-links a {
    color: white;
    text-decoration: none;
    font-size: 1.4rem;
    transition: color 0.3s ease;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .navbar .nav-links a:hover {
    color: #f39c12;
  }
  
  .menu-toggle {
    display: none;
    background: none;
    border: none;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .menu-box {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    background-color: #333;
    z-index: 1000;
    transform: translateX(100%);
    transition: transform 0.3s ease, opacity 0.3s ease;
    padding-top: 60px;
    opacity: 0;
  }
  
  .menu-box.active {
    display: block;
    transform: translateX(0);
    opacity: 1;
  }
  
  .menu-close {
    display: none;
    position: absolute;
    top: 20px;
    right: 20px;
    color: white;
    font-size: 1.5rem;
    background: none;
    border: none;
    cursor: pointer;
    transition: opacity 0.3s ease, transform 0.3s ease;
    z-index: 1100;
    opacity: 0;
  }
  
  .menu-close.show {
    display: block;
    opacity: 1;
    transform: scale(1);
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .navbar .nav-links {
      display: none;
    }
  
    .navbar .logo {
      font-size: 20px;
      text-transform: uppercase;
      text-decoration: none;
      color: #f39c12;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    }
  
    .navbar .nav-links a {
      color: white;
      text-decoration: none;
      font-size: 1rem;
      transition: color 0.3s ease;
      font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
  
    .navbar {
      background-color: #333;
      color: white;
      padding: 1rem 0;
      position: fixed;
      width: 100%;
      top: 0;
      z-index: 1000;
      transition: background-color 0.3s ease;
    }
  
    .navbar .menu-toggle {
      display: block;
    }
  
    .menu-box .nav-links {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-left: -100px;
    }
    
    .menu-box .nav-links li {
      margin: 20px 0;
    }
  
    .menu-box {
      display: block;
      position: fixed;
      top: 0;
      right: 0;
      width: 250px; 
      height: 100%;
      background-color: #333; 
      z-index: 1000; 
      transform: translateX(100%); 
      transition: transform 0.3s ease; 
   }
  
    .menu-box.active {
      transform: translateX(0);
    }
  
    .menu-close {
      display: block;
    }
  }
  